/* dropdawn */
h1,
h2,
h3,
h4,
p {
  margin-top: 0;
  margin-bottom: 0;
}

ul,
ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

button {
  margin: 0;
  padding: 0;
  display: block;
  border: none;
}

.list {
  list-style: none;
}

.link {
  text-decoration: none;
}

/* / dropdawn */