}

.scroll-up__container {
  visibility: hidden;
  opacity: 0;
  transition: visibility .5s, opacity .5s;
  position: fixed;
  bottom: 30px;
  right: 20px;
}

.scroll-up__container:after {
  content: "";
  width: 40px;
  height: 40px;
  background-color: #fff;
  border-radius: 50%;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  box-shadow: 0 0 10px 5px rgba(255, 255, 255, .75);
}

.scroll-up__container--shown {
  visibility: visible;
  opacity: 1;
  cursor: pointer;
  transition: visibility .5s, opacity .5s, transform .5s;
}

.scroll-up__container--shown:hover, .scroll-up__container--shown:focus {
  transform: scale(1.2);
}

.scroll-up__icon {
  width: 40px;
  height: 40px;
  fill: #f87719;
  z-index: 100000;
  position: relative;
}

